.swiper-slide-active .movie-slick {
  padding: 10px;
  background: rgba(20, 20, 20, 0.5) !important;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(1px);
  border: 1px solid #5d5757;
  transition: all 0.6s ease 0s;
}
#movieshow .swiper-slide.swiper-slide-prev {
  filter: blur(4px);
}
#movieshow .swiper-slide.swiper-slide-next {
  filter: blur(4px);
}

#episode3 .swiper-button-prev {
  color: var(--iq-white);
  right: auto;
  left: 9px;
  z-index: 9;
  display: flex;
  align-items: center;
}
#episode3 .swiper-button-next {
  left: auto;
  color: var(--iq-white);
  right: 9px;
  z-index: 9;
  display: flex !important;
  align-items: center !important;
}

#episode3 .swiper-button i {
  font-size: 24px;
}

.swiper-button-prev:after,
.swiper-container-rtl .swiper-button-next:before {
  content: "none";
}

.swiper-button-next:after,
.swiper-container-rtl .swiper-button-prev:before {
  content: "none";
}

#episode3 .swiper-button-prev {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 60px;
  text-align: center;
  opacity: 1;
  font-size: 0;
  z-index: 9;
  background: rgba(0, 0, 0, 0.5);
  margin: 0;
  left: 0;
  line-height: 5px;
  box-shadow: 0px 9px 19px #01041b0d;
  font-size: 0;
  transform: none;
  color: var(--iq-white);
  -webkit-transition: all 0.4s ease-in-out 0s;
  -moz-transition: all 0.4s ease-in-out 0s;
  transition: all 0.4s ease-in-out 0s;
  top: 60%;
}

#episode3 .swiper-button-next {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 60px;
  text-align: center;
